<template>
     <div>
    <v-breadcrumbs :items="breadcrumb" divider=">"></v-breadcrumbs>
         <h1>Clients</h1>

         <v-layout row justify-center>
    <v-dialog v-model="dialog" persistent max-width="600px">
      <template v-slot:activator="{ on }">
        <v-btn color="primary" dark v-on="on">Nuevo cliente</v-btn>
      </template>
      <v-card>
        <v-card-title>
          <span class="headline">Cliente</span>
        </v-card-title>
        <v-card-text>
          <v-container grid-list-md>
            <v-layout wrap>
              <v-flex xs12 sm12 md12>
                <v-text-field label="Nombre Completo*" v-model="client.name" required></v-text-field>
              </v-flex>

              <v-flex xs12>
                <v-text-field label="Direccin*" v-model="client.address" required></v-text-field>
              </v-flex>
              <v-flex xs12>
                <v-text-field label="Telfono" type="tel" v-model="client.tel"></v-text-field>
              </v-flex>
              <v-flex xs12 sm12>
                <v-select
                        v-model="client.type"
                  :items="['Persona', 'Tienda']"
                  label="Tipo*"
                  required
                ></v-select>
              </v-flex>

            </v-layout>
          </v-container>
          <small>*indica que son requeridos</small>
        </v-card-text>
        <v-card-actions>

          <v-btn color="red darken-1" flat @click="client={};dialog = false">Cancelar</v-btn>
            <v-spacer></v-spacer>
          <v-btn color="blue darken-1" flat @click="saveClient()">Guardar</v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
  </v-layout>


         <v-data-table
    :headers="headers"
    :items="clients"
    class="elevation-1"
  >
    <template v-slot:items="props">
      <td>{{ props.item.name }}</td>
      <td class="">{{ props.item.address }}</td>
      <td class="">{{ props.item.tel }}</td>
      <td class="">{{ props.item.type }}</td>
      <td class="text-xs-right">
          <v-btn fab dark color="warning" small @click="deleteClient(props.item)">
              <v-icon dark>remove</v-icon>
           </v-btn>
          <v-btn fab dark color="cyan" @click="client=props.item; dialog=true">
              <v-icon dark>edit</v-icon>
            </v-btn>
      </td>
    </template>
  </v-data-table>
  </div>

</template>

<script>
    export default {
        name: "Clients",
        created: function(){
            let token = this.$storage.get('user').token;
                this.config = {
                    headers: {'Authorization': "Bearer " + token}
                };
          this.getData()
        },
        data: () => ({
            breadcrumb: [
                { text: "Iniico",href:'/'},
                {text: "Clientes", disabled: true}
            ],
            client: {type:'Persona'},
            dialog: false,
            config: {},
            clients: [],
            headers: [
                {text: 'Nombre', value: 'name'},
                {text: 'Direccin', value: 'address'},
                {text: 'Telfono', value: 'tel'},
                {text: 'Tipo', value: 'type'},
                {text: ''}
            ]
    }), methods:{
            saveClient: function () {
                this.dialog= false
                let token = this.$storage.get('user').token;
                var config = {
                    headers: {'Authorization': "Bearer " + token}
                };
                if(this.client._id){
                    this.$http.patch(`${this.$apiBase}/client/${this.client._id}`,this.client,config).then((response)=>{
                    this.getData()
                    this.$toasted.success("Cliente Actualizado con xito")
                }).catch((err)=>{
                    this.$toasted.error("Error obteniendo los datos")
                })
                }else{
                   this.$http.post(`${this.$apiBase}/client`,this.client,config).then((response)=>{
                    this.getData()
                    this.$toasted.success("Cliente creado con xito")
                }).catch((err)=>{
                    this.$toasted.error("Error obteniendo los datos")
                })
                }


            },
            deleteClient: function(client){
                console.log(client)
                this.$swal({
                  title: "Estas seguro?",
                  text: "El cliente no podra recuperarse luego de ser eliminado!",
                  icon: "warning",
                  buttons: true,
                  dangerMode: true,
                })
                .then((willDelete) => {
                  if (willDelete) {
                      this.$http.delete(`${this.$apiBase}/client/${client._id}`,this.config).then((response)=>{
                        this.getData()
                        swal("Poof! Cliente eliminado!", {
                      icon: "success",
                    });
                    }).catch((err)=>{
                        this.$toasted.error("Error obteniendo los datos")
                    })

                  } else {
                    swal("Operacion cancelada!");
                  }
                })
            },
            getData: function () {
                let token = this.$storage.get('user').token;
                var config = {
                    headers: {'Authorization': "Bearer " + token}
                };
                this.$http.get(`${this.$apiBase}/client`,config).then((response)=>{
                    this.clients = response.data.data
                }).catch((err)=>{
                    this.$toasted.error("Error obteniendo los datos")
                })
            }
        }
    }
</script>

