<template>
    <v-content class="action">
      <v-container  bg fill-height grid-list-md text-xs-center>
        <v-layout align-center justify-center>
          <v-flex xs12 sm8 md4>
            <v-card class="elevation-12">
              <v-toolbar dark color="primary">
                <v-toolbar-title>{{this.title}} - {{$appName}}</v-toolbar-title>
                <v-spacer></v-spacer>
              </v-toolbar>
              <v-card-text>
                <v-form>
                  <v-text-field prepend-icon="person" name="login" v-model="username" label="Usuario" type="text" v-on:keyup.enter="doLogin()">></v-text-field>
                  <v-text-field prepend-icon="lock" name="password" v-model="password" label="Contrasea" id="password" type="password" v-on:keyup.enter="doLogin()"></v-text-field>
                </v-form>
              </v-card-text>
              <v-card-actions>
                <v-spacer></v-spacer>
                <v-btn color="primary" @click="doLogin">Entrar</v-btn>
              </v-card-actions>
            </v-card>
          </v-flex>
        </v-layout>
      </v-container>
    </v-content>
</template>

<script>
import axios from 'axios'
  export default {
    name: 'Login',
      data: () => ({
    title: 'Iniciar sesion',
        user: {},
        username:'',
        password:''
  }),
    methods: {
      doLogin: function(){
        this.$http.post(`${this.$apiBase}/auth`,{
	"username":this.username,
	"password":this.password
})
      .then((result) => {
        if(result.data && result.data.data && result.data.data.token){
          this.user = result.data.data
          this.$toasted.success("Ingreso correcto!")
          this.$storage.set('user', result.data.data)
          this.$router.push('/')
        }else {
          console.log("Error al ingresar")
        }
      }).catch((err)=>{
        console.log("Error ingresando...")
          this.$toasted.error('Uusario y/o contraseas invalidos');
        })
      }
    }
  }
</script>
<style>
.action {
    display: flex;
    align-items: center;
    justify-content: center;
    height: 100vh;
}
</style>
