<template>
     <div>
    <v-breadcrumbs :items="breadcrumb" divider=">"></v-breadcrumbs>
         <h1>Productos</h1>

         <v-layout row justify-center>
    <v-dialog v-model="dialog" persistent max-width="600px">
      <template v-slot:activator="{ on }">
        <v-btn color="primary" dark v-on="on">Nuevo producto</v-btn>
      </template>
      <v-card>
        <v-card-title>
          <span class="headline">Producto</span>
        </v-card-title>
        <v-card-text>
          <v-container grid-list-md>
            <v-layout wrap>
              <v-flex xs12 sm12 md12>
                <v-text-field label="Producto/Servicio*" v-model="product.name" required></v-text-field>
              </v-flex>

                <v-flex xs12>
                <v-text-field label="Precio de Compra*" v-model="product.price" required type="number"></v-text-field>
              </v-flex>

              <v-flex xs12>
                <v-text-field label="Precio de venta*" v-model="product.sale_price" required type="number"></v-text-field>
              </v-flex>

            </v-layout>
          </v-container>
          <small>*indica que son requeridos</small>
        </v-card-text>
        <v-card-actions>

          <v-btn color="red darken-1" flat @click="product={};dialog = false">Cancelar</v-btn>
            <v-spacer></v-spacer>
          <v-btn color="blue darken-1" flat @click="saveProduct()">Guardar</v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
  </v-layout>

         <v-data-table
    :headers="headers"
    :items="products"
    class="elevation-1"
  >
    <template v-slot:items="props">
      <td>{{ props.item.name }}</td>
      <td class="">{{ props.item.price }}</td>
      <td class="text-xs-right">
          <v-btn fab dark color="warning" small @click="deleteProduct(props.item)">
              <v-icon dark>remove</v-icon>
           </v-btn>
          <v-btn fab dark color="cyan" @click="product=props.item; dialog=true">
              <v-icon dark>edit</v-icon>
            </v-btn>
      </td>
    </template>
  </v-data-table>
  </div>

</template>

<script>
    export default {
        name: "Products",
        created: function(){
            let token = this.$storage.get('user').token;
                this.config = {
                    headers: {'Authorization': "Bearer " + token}
                };
          this.getData()
        },
        data: () => ({
            breadcrumb: [
                { text: "Iniico",href:'/'},
                {text: "Productos", disabled: true}
            ],
            product: {},
            dialog: false,
            config: {},
            products: [],
            headers: [
                {text: 'Nombre', value: 'name'},
                {text: 'Precio', value: 'price'},
                {text: ''}
            ]
    }), methods:{
            saveProduct: function () {
                this.dialog= false
                let token = this.$storage.get('user').token;
                var config = {
                    headers: {'Authorization': "Bearer " + token}
                };
                if(this.product._id){
                    this.$http.patch(`${this.$apiBase}/product/${this.product._id}`,this.product,config).then((response)=>{
                    this.getData()
                    this.$toasted.success("Producto Actualizado con xito")
                }).catch((err)=>{
                    this.$toasted.error("Error obteniendo los datos")
                })
                }else{
                   this.$http.post(`${this.$apiBase}/product`,this.product,config).then((response)=>{
                    this.getData()
                    this.$toasted.success("Producto creado con xito")
                }).catch((err)=>{
                    this.$toasted.error("Error obteniendo los datos")
                })
                }


            },
            deleteProduct: function(product){
                console.log(product)
                this.$swal({
                  title: "Estas seguro?",
                  text: "El producto no podra recuperarse luego de ser eliminado!",
                  icon: "warning",
                  buttons: true,
                  dangerMode: true,
                })
                .then((willDelete) => {
                  if (willDelete) {
                      this.$http.delete(`${this.$apiBase}/product/${product._id}`,this.config).then((response)=>{
                        this.getData()
                        swal("Poof! Producto eliminado!", {
                      icon: "success",
                    });
                    }).catch((err)=>{
                        this.$toasted.error("Error obteniendo los datos")
                    })

                  } else {
                    swal("Operacion cancelada!");
                  }
                })
            },
            getData: function () {
                this.product = {}
                let token = this.$storage.get('user').token;
                var config = {
                    headers: {'Authorization': "Bearer " + token}
                };
                this.$http.get(`${this.$apiBase}/product`,config).then((response)=>{
                    this.products = response.data.data
                }).catch((err)=>{
                    this.$toasted.error("Error obteniendo los datos")
                })
            }
        }
    }
</script>

