    <v-spacer></v-spacer>
    <v-toolbar-items class="hidden-sm-and-down">
      <v-btn flat :to="'/'"><v-icon>home</v-icon> Inicio</v-btn>
      <v-btn flat :to="'/sales'"><v-icon>shopping_cart</v-icon> Venta</v-btn>
      <v-btn flat :to="'/clients'"><v-icon>person</v-icon> Clientes</v-btn>
