<script>
export default {
    name: 'App',
    watch: {
    '$route' (to, from) {
        this.checkLogin()
      // react to route changes...
    }
  },
    created: function(){
        this.checkLogin()
            // let token = this.$storage.get('user').token;
            // if(!token){
            //   console.log(this.$route.path)
            // }
        },
    data () {
      return {
        drawer: null,
        items: [
          { title: 'Inicio', icon: 'home', href:'/'},
          { title: 'Venta', icon: 'shopping_cart', href: '/sales' },
          { title: 'Clientes', icon: 'person', href: '/clients' },
          { title: 'Productos', icon: 'inbox', href: '/products' },
          { title: 'Reportes', icon: 'graphic_eq', href: '/reports' },
          { title: 'Salir', icon: 'exit_to_app', href: '/login' }
        ]
      }
    },
    methods: {
        checkLogin () {
            if(this.$route.path != '/login'){
                let user = this.$storage.get('user');
                if(!user){
                    console.log("Invalid login")
                    this.$router.push('/login')
                }
            }
        }
    }
};
