<template>
     <div>
    <v-breadcrumbs :items="breadcrumb" divider=">"></v-breadcrumbs>
         <h1>Reporte de ventas</h1>

         <v-layout row wrap >
      <v-flex xs12 lg4 offset-lg1>
        <v-menu
          ref="menu1"
          v-model="date_menu_start"
          :close-on-content-click="true"
          :nudge-right="40"
          lazy
          transition="scale-transition"
          offset-y
          full-width
          max-width="290px"
          min-width="290px"
        >
          <template v-slot:activator="{ on }">
            <v-text-field
              v-model="date_start_formated"
              label="Date"
              hint="DD/MM/YYYY"
              readonly
              prepend-icon="event"
              v-on="on"
            ></v-text-field>
          </template>
          <v-date-picker v-model="date_start" no-title @input="menu1 = false"></v-date-picker>
        </v-menu>
      </v-flex>

      <v-flex xs12 lg4 offset-lg1 >
        <v-menu
          v-model="date_menu_end"
          :close-on-content-click="true"
          :nudge-right="40"
          lazy
          transition="scale-transition"
          offset-y
          full-width
          max-width="290px"
          min-width="290px"
        >
          <template v-slot:activator="{ on }">
            <v-text-field
              v-model="date_end_formated"
              label="Fecha Final"
              hint="DD/MM/YYYY"
              prepend-icon="event"
              readonly
              v-on="on"
            ></v-text-field>
          </template>
          <v-date-picker v-model="date_end" no-title @input="menu2 = false"></v-date-picker>
        </v-menu>
      </v-flex>
    </v-layout>

<v-flex xs12 sm10 offset-sm1>
         <v-data-table
    :headers="headers"
    :items="sales"
    class="elevation-1"

  >

    <template v-slot:items="props">
      <td>{{ formatDate(props.item.date) }}</td>
      <td>{{ clients.find(client=>client._id==props.item.client).name }}</td>
      <td>{{ products.find(product => product._id==props.item.product).name }}</td>
      <td class="">{{ props.item.qty }}</td>
      <td class="">Q {{ props.item.sale_price }}</td>
      <td class="">Q {{ props.item.sale_price * props.item.qty  }}</td>
    </template>
  </v-data-table>
</v-flex>
<br>
         <v-layout>
    <v-flex xs12 sm6 offset-sm3>
      <v-card>
        <v-card-title primary-title>
          <div>
            <h3 class="headline mb-0">Totales de venta de {{date_start_formated}} a {{date_end_formated}}</h3>
            <v-list>

                <v-list-tile avatar>
              <v-list-tile-content>
              <v-list-tile-title v-text="'Total de venta: '"></v-list-tile-title>
            </v-list-tile-content>
                <v-list-tile-avatar>
              Q. {{total_sale}}
            </v-list-tile-avatar>
                    </v-list-tile>

                <v-list-tile avatar>
              <v-list-tile-content>
              <v-list-tile-title v-text="'Total de costos: '"></v-list-tile-title>
            </v-list-tile-content>
                <v-list-tile-avatar>
              Q. {{total_price}}
            </v-list-tile-avatar>
                    </v-list-tile>

                <v-list-tile avatar>
              <v-list-tile-content>
              <v-list-tile-title v-text="'Total garrafones: '"></v-list-tile-title>
            </v-list-tile-content>
                <v-list-tile-avatar>
              {{total_qty}}
            </v-list-tile-avatar>
                    </v-list-tile>

                <v-list-tile avatar>
              <v-list-tile-content>
              <v-list-tile-title v-text="'Ganancia: '"></v-list-tile-title>
            </v-list-tile-content>
                <v-list-tile-avatar>
              Q. {{total_sale-total_price}}
            </v-list-tile-avatar>
                    </v-list-tile>

            </v-list>
          </div>
        </v-card-title>

      </v-card>
    </v-flex>
  </v-layout>
  </div>

</template>

<script>
    export default {
        name: "Reports",
        watch: {
      date_start (val) {
          this.date_start_formated = this.formatDate(this.date_start)
          this.getData()
      },
            date_end (val) {
          this.date_end_formated = this.formatDate(this.date_end)
                this.getData()
      },
            sales (){
          let total_price  =0;
          let total_sale =0;
          let total_qty =0;
                for(let sale of this.sales){
                    total_price += parseFloat(sale.qty)*parseFloat(sale.price)
                    total_sale += parseFloat(sale.qty)*parseFloat(sale.sale_price)
                    total_qty += parseFloat(sale.qty)
                }
                this.total_sale = total_sale
                this.total_price = total_price
                this.total_qty = total_qty
            }

    },

        created: function(){

            this.date_start_formated= this.formatDate(new Date().toISOString().substr(0, 10))
            this.date_end_formated= this.formatDate(new Date().toISOString().substr(0, 10))

            let token = this.$storage.get('user').token;
                this.config = {
                    headers: {'Authorization': "Bearer " + token}
                };
          this.getData()
        },
        data: () => ({
            total_sale: 0,
            total_qty: 0,
            total_price: 0,
            date_menu: false,
            date_menu_start: false,
            date_menu_end: false,
            breadcrumb: [
                { text: "Iniico",href:'/'},
                {text: "Reportes", disabled: true}
            ],

            date_start: new Date().toISOString().substr(0, 10),
            date_start_formated: false,
            date_end: new Date().toISOString().substr(0, 10),
            date_end_formated: false,
            dialog: false,
            config: {},
            sales: [],
            products: [],
            clients:[],
            headers: [
                {text: 'Fecha', value: 'date'},
                {text: 'Cliente', value: 'client'},
                {text: 'Producto', value: 'product'},
                {text: 'Cant', value: 'qty'},
                {text: 'Precio', value: 'price'},
                {text: 'Total', value: 'total'}
            ]
    }), methods:{
            formatDate (date) {
        if (!date) return null

        const [year, month, day] = date.split('-')
        return `${day}/${month}/${year}`
      },
      parseDate (date) {
        if (!date) return null

        const [month, day, year] = date.split('/')
        return `${year}-${month.padStart(2, '0')}-${day.padStart(2, '0')}`
      },
            setSaleClient: function(){
                this.sale.client = this.sale_client._id
            },

            setSaleProduct: function(){
                console.log(this.sale_product)
                this.sale.product = this.sale_product._id
                this.sale.price = this.sale_product.price
                this.sale.sale_price = this.sale_product.sale_price
            },


            getData: function () {
                this.sale = {qty:1,date:new Date().toISOString().slice(0,10)}
                let token = this.$storage.get('user').token;
                var config = {
                    headers: {'Authorization': "Bearer " + token}
                };
                this.$http.get(`${this.$apiBase}/product`,config).then((response)=>{
                    this.products = response.data.data
                }).catch((err)=>{
                    this.$toasted.error("Error obteniendo los datos")
                })
                this.$http.get(`${this.$apiBase}/client`,config).then((response)=>{
                    this.clients = response.data.data
                }).catch((err)=>{
                    this.$toasted.error("Error obteniendo los datos")
                })
                this.$http.get(`${this.$apiBase}/sale/${this.date_start}/${this.date_end}`,config).then((response)=>{
                    this.sales = response.data.data
                }).catch((err)=>{
                    this.$toasted.error("Error obteniendo los datos")
                })
            }
        }
    }
</script>

