<template>
     <div>
    <v-breadcrumbs :items="breadcrumb" divider=">"></v-breadcrumbs>
         <h1>Ventas</h1>

         <v-layout row justify-center>
    <v-dialog v-model="dialog" persistent max-width="600px">
      <template v-slot:activator="{ on }">
        <v-btn color="primary" dark v-on="on">Nuevo Venta</v-btn>
      </template>
      <v-card>
        <v-card-title>
          <span class="headline">Venta</span>
        </v-card-title>
        <v-card-text>
          <v-container grid-list-md>
            <v-layout wrap>
              <v-flex xs12 sm12 md12>
                 <v-select
                  v-model="sale_client"
                  v-on:change="setSaleClient()"
                  :hint="`${sale.client}`"
                  :items="clients"
                  item-text="name"
                  item-value="_id"
                  label="Cliente"
                  persistent-hint
                  return-object
                  single-line
                ></v-select>
              </v-flex>

                <v-flex xs12>
                <v-select
                  v-model="sale_product"
                  v-on:change="setSaleProduct()"
                  :hint="`${sale.product}`"
                  :items="products"
                  item-text="name"
                  item-value="_id"
                  label="Producto"
                  persistent-hint
                  return-object
                  single-line
                ></v-select>
              </v-flex>

              <v-flex xs6>
                <v-text-field label="Precio de venta*" v-model="sale.sale_price" required type="number"></v-text-field>
              </v-flex>
                <v-flex xs6>
                <v-text-field label="Cantidad*" v-model="sale.qty" required type="number"></v-text-field>
              </v-flex>
                <v-flex xs12>
      <v-menu
        v-model="date_menu"
        :close-on-content-click="false"
        :nudge-right="40"
        lazy
        transition="scale-transition"
        offset-y
        full-width
        min-width="290px"
      >
        <template v-slot:activator="{ on }">
          <v-text-field
            v-model="sale.date"
            label="Fecha de venta"
            prepend-icon="event"
            readonly
            v-on="on"
          ></v-text-field>
        </template>
        <v-date-picker v-model="sale.date" @input="date_menu = false"></v-date-picker>
      </v-menu>
    </v-flex>

            </v-layout>
          </v-container>
          <small>*indica que son requeridos</small>
        </v-card-text>
        <v-card-actions>

          <v-btn color="red darken-1" flat @click="sale={};dialog = false">Cancelar</v-btn>
            <v-spacer></v-spacer>
          <v-btn color="blue darken-1" flat @click="saveSale()">Guardar</v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
  </v-layout>
         <v-data-table
    :headers="headers"
    :items="sales"
    class="elevation-1"
  >
    <template v-slot:items="props">
      <td>{{ formatDate(props.item.date) }}</td>
      <td>{{ clients.find(client=>client._id==props.item.client).name }}</td>
      <td>{{ products.find(product => product._id==props.item.product).name }}</td>
      <td class="">{{ props.item.qty }}</td>
      <td class="">Q {{ props.item.sale_price }}</td>
      <td class="">Q {{ props.item.sale_price * props.item.qty  }}</td>
      <td class="text-xs-right">
          <v-btn fab dark color="warning" small @click="deleteSale(props.item)">
              <v-icon dark>remove</v-icon>
           </v-btn>
          <v-btn fab dark color="cyan" @click="modifySale(props.item)">
              <v-icon dark>edit</v-icon>
            </v-btn>
      </td>
    </template>
  </v-data-table>
  </div>

</template>

<script>
    export default {
        name: "Sales",
        created: function(){
            let token = this.$storage.get('user').token;
                this.config = {
                    headers: {'Authorization': "Bearer " + token}
                };
          this.getData()
        },
        data: () => ({
            date_menu: false,
            breadcrumb: [
                { text: "Iniico",href:'/'},
                {text: "Ventas", disabled: true}
            ],
            sale_client: false,
            sale_product: false,
            sale: {qty:1,date:new Date().toISOString().slice(0,10)},
            dialog: false,
            config: {},
            sales: [],
            products: [],
            clients:[],
            headers: [
                {text: 'Fecha', value: 'date'},
                {text: 'Cliente', value: 'client'},
                {text: 'Producto', value: 'product'},
                {text: 'Cant', value: 'qty'},
                {text: 'Precio', value: 'price'},
                {text: 'Total', value: 'total'},
                {text: ''}
            ]
    }), methods:{
            formatDate (date) {
        if (!date) return null

        const [year, month, day] = date.split('-')
        return `${day}/${month}/${year}`
      },
            setSaleClient: function(){
                this.sale.client = this.sale_client._id
            },
            modifySale: function(sale){
                this.sale = sale
                this.sale_product = this.products.find( x => x._id=sale.product)
                this.sale_client = this.clients.find(x => x._id==sale.client)
                this.dialog = true
            },
            setSaleProduct: function(){
                console.log(this.sale_product)
                this.sale.product = this.sale_product._id
                this.sale.price = this.sale_product.price
                this.sale.sale_price = this.sale_product.sale_price
            },
            saveSale: function () {
                this.dialog= false
                let token = this.$storage.get('user').token;
                var config = {
                    headers: {'Authorization': "Bearer " + token}
                };
                if(this.sale._id){
                    this.$http.patch(`${this.$apiBase}/sale/${this.sale._id}`,this.sale,config).then((response)=>{
                    this.getData()
                    this.$toasted.success("Venta Actualizado con xito")
                }).catch((err)=>{
                    this.$toasted.error("Error obteniendo los datos")
                })
                }else{
                   this.$http.post(`${this.$apiBase}/sale`,this.sale,config).then((response)=>{
                    this.getData()
                    this.$toasted.success("Venta creada con xito")
                }).catch((err)=>{
                    this.$toasted.error("Error obteniendo los datos")
                })
                }


            },
            deleteSale: function(sale){
                console.log(sale)
                this.$swal({
                  title: "Estas seguro?",
                  text: "Esta venta ser anulada!",
                  icon: "warning",
                  buttons: true,
                  dangerMode: true,
                })
                .then((willDelete) => {
                  if (willDelete) {
                      this.$http.delete(`${this.$apiBase}/sale/${sale._id}`,this.config).then((response)=>{
                        this.getData()
                        swal("Poof! Venta anulada!", {
                      icon: "success",
                    });
                    }).catch((err)=>{
                        this.$toasted.error("Error obteniendo los datos")
                    })

                  } else {
                    swal("Operacion cancelada!");
                  }
                })
            },
            getData: function () {
                this.sale = {qty:1,date:new Date().toISOString().slice(0,10)}
                let token = this.$storage.get('user').token;
                var config = {
                    headers: {'Authorization': "Bearer " + token}
                };
                this.$http.get(`${this.$apiBase}/product`,config).then((response)=>{
                    this.products = response.data.data
                }).catch((err)=>{
                    this.$toasted.error("Error obteniendo los datos")
                })
                this.$http.get(`${this.$apiBase}/client`,config).then((response)=>{
                    this.clients = response.data.data
                }).catch((err)=>{
                    this.$toasted.error("Error obteniendo los datos")
                })
                this.$http.get(`${this.$apiBase}/sale`,config).then((response)=>{
                    this.sales = response.data.data
                }).catch((err)=>{
                    this.$toasted.error("Error obteniendo los datos")
                })
            }
        }
    }
</script>

